/*
    contributor 기준
    PR 3개 이상, 혹은 그에 준하는 기여를 했다고 리더가 판단하는 경우 contributor 가능
    
    정렬 순서
    1. 기본적으로 참여 기간 순(월 단위만 체크)
    2. 다만, 해커톤 기간이었던 2023년 1월은 두 달치로 인정

    startAt : 1월에 참여했으면 1월 1일로 두기
    endAt : 1월에 그만두면 1월 31일(해당 월 말일)로 두기
*/

export interface Contributor {
  name: string
  startAt: string
  endAt: string | null
  github: string | null
  position: string
}

