import dayjs from 'dayjs'
import { Helmet } from 'react-helmet-async'

import GitHubIcon from '@mui/icons-material/GitHub'
import { Box, Card, Container, Divider, Grid, Link, Typography } from '@mui/material'

import { type Contributor, contributors } from 'src/data/contributors'

import { getDateDiff } from 'src/utils/date/getDateDiff'

interface ContributorCardProps {
  contributor: Contributor
}

const ContributorCard: React.FC<ContributorCardProps> = ({ contributor }) => {
  const startAtDate = dayjs(contributor.startAt)
  const endAtDate = contributor.endAt ? dayjs(contributor.endAt) : dayjs()

  const projectDuration = getDateDiff(startAtDate, endAtDate)

  return (
    <Grid item xs={12} sm={4}>
      <Card sx={{ m: 1, p: 2, pb: 1, minHeight: '112px', backgroundColor: '#f2f2f2' }}>
        <Box mb={0.5}>
          <Typography variant="h4" color="primary" component="span">
            {contributor.name}&nbsp;
            <Typography variant="h5" component="span" color="black">
              {contributor.position}
            </Typography>
          </Typography>
        </Box>
        <Box display={'flex'} flex={1} justifyContent={'space-between'}>
          <Typography mb={1}>
            {startAtDate.format('YYYY.MM')} ~ {contributor.endAt && endAtDate.format('YYYY.MM')}
          </Typography>
          <Typography color={'secondary'} mb={1}>
            {projectDuration}
          </Typography>
        </Box>
        {contributor.github && (
          <Link href={contributor.github} target="_blank" rel="noopener noreferrer" sx={{ color: 'black' }}>
            <GitHubIcon />
          </Link>
        )}
      </Card>
    </Grid>
  )
}

const Contributors: React.FC<void> = () => {
  const calcPeriod = (contributor: Contributor) => {
    const benefitList = ['김재민', '김규진', '김승현', '전병국']
    const startAt = new Date(contributor.startAt)
    const endAt = contributor.endAt ? new Date(contributor.endAt) : new Date()

    let months = Math.round((endAt.getTime() - startAt.getTime()) / (1000 * 60 * 60 * 24 * 30))

    if (benefitList.includes(contributor.name)) {
      months = months + 1
    }

    return months
  }

  const sortedContributors = contributors.sort((a, b) => calcPeriod(b) - calcPeriod(a))

  return (
    <>
      <Helmet>
        <title>Contributors</title>
      </Helmet>
      <Container maxWidth="md">
        <Card elevation={0}>
          <Box p={2} pt={4}>
            <Typography variant="h3" textAlign="center" gutterBottom>
              Developed by <Link href="https://kwebofficial.com">KWEB</Link>
            </Typography>
            <Typography variant="h5" align="center">
              문의 및 버그 제보 : officialkweb@gmail.com
            </Typography>
          </Box>
          <Divider sx={{ m: 1 }} />
          <Typography variant="h3" color="black" align="center" m={2}>
            Contributors
          </Typography>
          <Box sx={{ p: 2 }}>
            <Grid container spacing={1}>
              {sortedContributors.map((c, index) => (
                <ContributorCard contributor={c} key={index} />
              ))}
            </Grid>
          </Box>
        </Card>
      </Container>
    </>
  )
}

export default Contributors
