import dayjs, { Dayjs } from 'dayjs'

const today = new Date()
/**
 *
 * 시작날짜와 종료날짜의 차이를 YY년 MM개월로 반환합니다.
 * 만약 00이라면 MM개월만 반환합니다.
 * 2023년 01월 01일 ~ 2023년 03월 31일 => 3개월
 */
export const getDateDiff = (startAt: Dayjs, _endAt: Dayjs | null = null): string => {
  const endAt = _endAt || dayjs()
  const totalMonths = endAt.diff(startAt, 'month', true) // Get precise month difference as a floating-point number
  const fullMonths = Math.floor(totalMonths) // Get the integer part of the month difference
  const remainingDays = totalMonths - fullMonths // Get the fractional part to account for remaining days

  let years = Math.floor(fullMonths / 12)
  let months = fullMonths % 12

  // If there are remaining days, round up the months
  if (remainingDays > 0) {
    months += 1
    if (months === 12) {
      years += 1
      months = 0
    }
  }

  if (years === 0) {
    return `${months}개월`
  }

  return `${years}년 ${months}개월`
}
